import { useState } from 'react'
import { useRouter } from 'next/router'
import { useAuth } from '../utils/auth'


export default function Login() {
    const { login } = useAuth()
    const router = useRouter()
    const [form, setForm] = useState({ email: '', password: '', remember: false })
    const [error, setError] = useState<string | null>(null)
    const [loading, setLoading] = useState(false)


    const submit = async (e: React.FormEvent) => {
        e.preventDefault()
        setError(null)
        setLoading(true)
        try {
            await login(form.email, form.password, form.remember)
            router.push('/dashboard')
        } catch (err: any) {
            setError(err?.response?.data?.message || 'Invalid credentials')
        } finally { setLoading(false) }
    }


    return (
        <div className="min-h-screen flex items-center justify-center bg-gray-50">
            <div className="w-full max-w-md bg-white p-6 rounded shadow">
                <h2 className="text-xl font-semibold mb-4">Login</h2>
                {error && <div className="mb-2 text-red-600">{error}</div>}
                <form onSubmit={submit} className="space-y-3">
                    <input className="input" placeholder="Email" value={form.email} onChange={e => setForm({ ...form, email: e.target.value })} />
                    <input className="input" placeholder="Password" type="password" value={form.password} onChange={e => setForm({ ...form, password: e.target.value })} />
                    <label className="flex items-center gap-2"><input type="checkbox" checked={form.remember} onChange={e => setForm({ ...form, remember: e.target.checked })} /> Remember me</label>
                    <button className="w-full py-2 bg-green-600 text-white rounded" disabled={loading}>{loading ? 'Logging in...' : 'Login'}</button>
                </form>
                <p className="mt-3 text-sm">Don't have an account? <a href="/signup" className="text-blue-600">Sign up</a></p>
            </div>
        </div>
    )
}